import React, { useState, useEffect, useRef } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { 
  faArrowLeft, 
  faSearch, 
  faMapPin, 
  faHome, 
  faBuilding, 
  faLocationArrow,
  faCheck,
  faTimes
} from '@fortawesome/free-solid-svg-icons';
import './AddressSelection.css';
import googleMapsManager from '../components/GoogleMapsManager';

const AddressSelection = ({ 
  addressType, 
  existingAddress, 
  onAddressSelect, 
  onBack 
}) => {
  const [searchQuery, setSearchQuery] = useState('');
  const [predictions, setPredictions] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [showDropdown, setShowDropdown] = useState(false);
  const [selectedLocation, setSelectedLocation] = useState(null);
  const [error, setError] = useState(null);
  const [useFallback, setUseFallback] = useState(false);
  
  // Manual input fields
  const [houseNumber, setHouseNumber] = useState('');
  const [landmark, setLandmark] = useState('');
  
  // Map state
  const [map, setMap] = useState(null);
  const [marker, setMarker] = useState(null);
  // const [currentPosition, setCurrentPosition] = useState(null);
  const [isGettingLocation, setIsGettingLocation] = useState(false);
  
  const searchInputRef = useRef(null);
  const dropdownRef = useRef(null);
  const mapRef = useRef(null);
  const autocompleteService = useRef(null);
  const geocoder = useRef(null);
  const searchTimeoutRef = useRef(null);

  // Extract pincode from address components
  const extractPincode = (addressComponents) => {
    if (!addressComponents) return null;
    
    const pincodeComponent = addressComponents.find(
      component => component.types.includes('postal_code')
    );
    
    return pincodeComponent ? pincodeComponent.long_name : null;
  };

  // Validate pincode (6 digits for India)
  const validatePincode = (pincode) => {
    if (!pincode) return false;
    return /^\d{6}$/.test(pincode);
  };

  // Initialize Google Maps API
  useEffect(() => {
    const initializeAPI = async () => {
      try {
        const services = await googleMapsManager.initialize();
        autocompleteService.current = services.autocompleteService;
        geocoder.current = services.geocoder;
        setUseFallback(false);
        initializeMap();
      } catch (error) {
        console.warn('Google Maps API initialization failed, using fallback mode:', error);
        setUseFallback(true);
      }
    };

    initializeAPI();

    return () => {
      if (searchTimeoutRef.current) {
        clearTimeout(searchTimeoutRef.current);
      }
    };
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  // Load existing address data on mount (for map and selected address section)
  useEffect(() => {
    if (existingAddress) {
      console.log('Loading existing address data (DB structure):', existingAddress);
      
      // Set coordinates if available
      if (existingAddress.latitude && existingAddress.longitude) {
        const addressData = {
          // Core coordinates
          lat: parseFloat(existingAddress.latitude),
          lng: parseFloat(existingAddress.longitude),
          latitude: parseFloat(existingAddress.latitude),
          longitude: parseFloat(existingAddress.longitude),
          
          // Address details (matching database field names)
          formattedAddress: existingAddress.formatted_address || '',
          addressComponents: existingAddress.address_components || [],
          pincode: existingAddress.pincode || '',
          
          // For backwards compatibility
          formatted_address: existingAddress.formatted_address || '',
          address_components: existingAddress.address_components || [],
          
          coordinates: {
            lat: parseFloat(existingAddress.latitude),
            lng: parseFloat(existingAddress.longitude)
          }
        };
        
        console.log('Setting selectedLocation with existing address:', addressData);
        setSelectedLocation(addressData);
      }
    }
  }, [existingAddress]);

  // Load existing address form fields (but not search bar)
  useEffect(() => {
    if (existingAddress) {
      console.log('Loading existing address into form fields:', existingAddress);
      setHouseNumber(existingAddress.line1 || '');
      setLandmark(existingAddress.line2 || '');
      
      // Don't load into search query - user can search for new address if needed
    }
  }, [existingAddress]);

  // Initialize map
  const initializeMap = () => {
    if (!window.google || !mapRef.current) return;

    // Use existing address coordinates, or try current location, or fallback to Chennai
    let defaultCenter;
    if (existingAddress?.latitude && existingAddress?.longitude) {
      defaultCenter = { lat: parseFloat(existingAddress.latitude), lng: parseFloat(existingAddress.longitude) };
    } else if (selectedLocation) {
      defaultCenter = selectedLocation;
    } else {
      defaultCenter = { lat: 13.0827, lng: 80.2707 }; // Chennai fallback
    }

    const mapInstance = new window.google.maps.Map(mapRef.current, {
      center: defaultCenter,
      zoom: 15,
      mapTypeControl: false,
      streetViewControl: false,
      fullscreenControl: false,
      styles: [
        {
          featureType: 'poi',
          elementType: 'labels',
          stylers: [{ visibility: 'off' }]
        }
      ]
    });

    const markerInstance = new window.google.maps.Marker({
      position: defaultCenter,
      map: mapInstance,
      draggable: true,
      animation: window.google.maps.Animation.DROP
    });

    // Handle marker drag
    markerInstance.addListener('dragend', () => {
      const position = markerInstance.getPosition();
      updateAddressFromCoordinates(position.lat(), position.lng());
    });

    // Handle map click
    mapInstance.addListener('click', (event) => {
      markerInstance.setPosition(event.latLng);
      updateAddressFromCoordinates(event.latLng.lat(), event.latLng.lng());
    });

    setMap(mapInstance);
    setMarker(markerInstance);

    // Only get current location if no existing address
    if (!existingAddress?.latitude || !existingAddress?.longitude) {
      getCurrentLocationOnLoad();
    }
  };

  // Get current location on load
  const getCurrentLocationOnLoad = () => {
    if (!navigator.geolocation) {
      console.log('Geolocation not supported');
      return;
    }

    setIsGettingLocation(true);
    navigator.geolocation.getCurrentPosition(
      (position) => {
        const { latitude, longitude } = position.coords;
        const coords = { lat: latitude, lng: longitude };
        
        if (map && marker) {
          map.setCenter(coords);
          marker.setPosition(coords);
          updateAddressFromCoordinates(latitude, longitude);
        }
        setIsGettingLocation(false);
      },
      (error) => {
        console.log('Could not get current location on load:', error);
        setIsGettingLocation(false);
      },
      { timeout: 10000, enableHighAccuracy: true, maximumAge: 60000 }
    );
  };

  // Update address from coordinates
  const updateAddressFromCoordinates = (lat, lng) => {
    if (!geocoder.current) return;

    const latlng = { lat, lng };
    geocoder.current.geocode({ location: latlng }, (results, status) => {
      if (status === window.google.maps.GeocoderStatus.OK && results.length > 0) {
        const result = results[0];
        const pincode = extractPincode(result.address_components);
        const addressData = {
          coordinates: { lat, lng },
          lat: lat,
          lng: lng,
          latitude: lat,
          longitude: lng,
          formattedAddress: result.formatted_address,
          addressComponents: result.address_components,
          houseNumber: houseNumber,
          landmark: landmark,
          pincode: pincode
        };
        // setCurrentPosition({ lat, lng });
        setSelectedLocation(addressData);
      }
    });
  };

  // Get current location
  const getCurrentLocation = () => {
    if (!navigator.geolocation) {
      alert('Geolocation is not supported by your browser');
      return;
    }

    setIsGettingLocation(true);
    navigator.geolocation.getCurrentPosition(
      (position) => {
        const { latitude, longitude } = position.coords;
        const coords = { lat: latitude, lng: longitude };
        
        if (map && marker) {
          map.setCenter(coords);
          marker.setPosition(coords);
          updateAddressFromCoordinates(latitude, longitude);
        }
        
                    // setCurrentPosition(coords);
            setIsGettingLocation(false);
      },
      (error) => {
        console.error('Error getting location:', error);
        alert('Unable to get your current location. Please select manually.');
        setIsGettingLocation(false);
      },
      { timeout: 10000, enableHighAccuracy: true }
    );
  };

  // Handle search input changes
  const handleSearchChange = (e) => {
    const query = e.target.value;
    setSearchQuery(query);
    setError(null);

    if (query.length === 0) {
      setSelectedLocation(null);
    }

    if (query.length < 2) {
      setPredictions([]);
      setShowDropdown(false);
      return;
    }

    if (searchTimeoutRef.current) {
      clearTimeout(searchTimeoutRef.current);
    }

    searchTimeoutRef.current = setTimeout(() => {
      setIsLoading(true);
      setShowDropdown(true);

      if (useFallback) {
        // Use fallback search (simplified for demo)
        setPredictions([]);
        setIsLoading(false);
      } else if (autocompleteService.current) {
        autocompleteService.current.getPlacePredictions(
          {
            input: query,
            componentRestrictions: { country: 'in' },
            types: ['geocode', 'establishment']
          },
          (predictions, status) => {
            setIsLoading(false);
            
            if (status === window.google.maps.places.PlacesServiceStatus.OK) {
              setPredictions(predictions || []);
            } else if (status === window.google.maps.places.PlacesServiceStatus.ZERO_RESULTS) {
              setPredictions([]);
            } else {
              setError('Unable to fetch locations');
              setPredictions([]);
            }
          }
        );
      } else {
        setIsLoading(false);
        setError('Location service not available');
      }
    }, 300);
  };

  // Handle location selection from dropdown
  const handleLocationSelect = (prediction) => {
    setSearchQuery(prediction.description);
    setPredictions([]);
    setShowDropdown(false);
    setError(null);

    if (geocoder.current) {
      geocoder.current.geocode(
        { 
          address: prediction.description,
          componentRestrictions: { country: 'in' }
        },
        (results, status) => {
          if (status === window.google.maps.GeocoderStatus.OK && results.length > 0) {
            const result = results[0];
            const coords = {
              lat: result.geometry.location.lat(),
              lng: result.geometry.location.lng()
            };
            
            const pincode = extractPincode(result.address_components);
            const addressData = {
              coordinates: coords,
              lat: coords.lat,
              lng: coords.lng,
              latitude: coords.lat,
              longitude: coords.lng,
              formattedAddress: result.formatted_address,
              addressComponents: result.address_components,
              houseNumber: houseNumber,
              landmark: landmark,
              pincode: pincode
            };
            
            setSelectedLocation(addressData);
            // setCurrentPosition(coords);
            
            // Update map
            if (map && marker) {
              map.setCenter(coords);
              marker.setPosition(coords);
            }
          }
        }
      );
    }
  };

  // Handle form submission
  const handleSubmit = () => {
    console.log('=== ADDRESS SELECTION SUBMIT ===');
    console.log('existingAddress prop:', existingAddress);
    console.log('selectedLocation:', selectedLocation);
    console.log('houseNumber:', houseNumber);
    console.log('landmark:', landmark);

    if (!selectedLocation) {
      alert('Please select a location first');
      return;
    }

    if (!selectedLocation.pincode || !validatePincode(selectedLocation.pincode)) {
      alert('Please enter a valid 6-digit pincode');
      return;
    }

    if (!selectedLocation.lat || !selectedLocation.lng) {
      alert('Please select a valid location on the map');
      return;
    }

    const addressData = {
      // Database fields (exact match with your structure)
      line1: houseNumber.trim(),
      line2: landmark.trim(),
      pincode: selectedLocation.pincode,
      latitude: selectedLocation.lat,
      longitude: selectedLocation.lng,
      formatted_address: selectedLocation.formattedAddress,
      address_components: selectedLocation.addressComponents || [],
      
      // Additional fields for compatibility
      id: existingAddress?.id || null,
      houseNumber: houseNumber.trim(),
      landmark: landmark.trim(),
      formattedAddress: selectedLocation.formattedAddress,
      addressComponents: selectedLocation.addressComponents || [],
      lat: selectedLocation.lat,
      lng: selectedLocation.lng,
      coordinates: {
        lat: selectedLocation.lat,
        lng: selectedLocation.lng
      }
    };

    console.log('Final addressData being sent:', addressData);
    onAddressSelect(addressData);
  };

  const getAddressTypeIcon = () => {
    return addressType === 'home' ? faHome : faBuilding;
  };

  const getAddressTypeTitle = () => {
    return addressType === 'home' ? 'Home Address' : 'Office Address';
  };

  return (
    <div className="address-selection-page">
      <div className="address-selection-container">
        {/* Header */}
        <div className="address-header">
          <button className="back-button" onClick={onBack}>
            <FontAwesomeIcon icon={faArrowLeft} />
          </button>
          <h1>
            <FontAwesomeIcon icon={getAddressTypeIcon()} />
            {getAddressTypeTitle()}
          </h1>
        </div>

        {/* Loading Current Location Banner */}
        {isGettingLocation && (
          <div className="location-loading-banner">
            <FontAwesomeIcon icon={faLocationArrow} spin />
            <span>Finding your current location...</span>
          </div>
        )}

        {/* Search Section */}
        <div className="search-section">
          <div className="search-container">
            <div className="search-icon">
              <FontAwesomeIcon icon={faSearch} />
            </div>
            <input
              ref={searchInputRef}
              type="text"
              className="search-input"
              placeholder="Search for your location..."
              value={searchQuery}
              onChange={handleSearchChange}
              onFocus={() => setShowDropdown(true)}
            />
            {isLoading && (
              <div className="loading-spinner">
                <div className="spinner"></div>
              </div>
            )}
          </div>

          {/* Current Location Button */}
          <button
            className="current-location-button"
            onClick={getCurrentLocation}
            disabled={isGettingLocation}
          >
            <FontAwesomeIcon icon={faLocationArrow} />
            {isGettingLocation ? 'Getting Location...' : 'Use Current Location'}
          </button>

          {/* Search Dropdown */}
          {showDropdown && predictions.length > 0 && (
            <div ref={dropdownRef} className="search-dropdown">
              {predictions.map((prediction) => (
                <button
                  key={prediction.place_id}
                  className="prediction-item"
                  onClick={() => handleLocationSelect(prediction)}
                >
                  <FontAwesomeIcon icon={faMapPin} />
                  <div className="prediction-content">
                    <div className="prediction-main">
                      {prediction.structured_formatting?.main_text || prediction.description}
                    </div>
                    {prediction.structured_formatting?.secondary_text && (
                      <div className="prediction-secondary">
                        {prediction.structured_formatting.secondary_text}
                      </div>
                    )}
                  </div>
                </button>
              ))}
            </div>
          )}
        </div>

        {/* Map Section */}
        <div className="map-section">
          <div ref={mapRef} className="map-container"></div>
          <div className="map-overlay">
            <p>📍 Drag the pin or click on the map to adjust location</p>
          </div>
        </div>

        {/* Selected Address Display - Below map */}
        {selectedLocation && (
          <div className="selected-address">
            <div className="address-details">
              {houseNumber && <p><strong>House/Building:</strong> {houseNumber}</p>}
              <p><strong>Address:</strong> {selectedLocation.formattedAddress}</p>
              {landmark && <p><strong>Landmark:</strong> {landmark}</p>}
            </div>
          </div>
        )}

        {/* Manual Input Section */}
        <div className="manual-input-section">
          <h3>Additional Details</h3>
          
          <div className="input-group">
            <label htmlFor="houseNumber">House/Building Number</label>
            <input
              type="text"
              id="houseNumber"
              value={houseNumber}
              onChange={(e) => setHouseNumber(e.target.value)}
              placeholder="e.g., Flat 101, House No. 123"
            />
          </div>

          <div className="input-group">
            <label htmlFor="landmark">Landmark / Directions</label>
            <input
              type="text"
              id="landmark"
              value={landmark}
              onChange={(e) => setLandmark(e.target.value)}
              placeholder="e.g., Near Metro Station, Behind Mall"
            />
          </div>

          <div className="input-group">
            <label htmlFor="pincode">Pincode *</label>
            <input
              type="text"
              id="pincode"
              value={selectedLocation?.pincode || ''}
              onChange={(e) => {
                const value = e.target.value.replace(/\D/g, '').slice(0, 6);
                setSelectedLocation(prev => prev ? { ...prev, pincode: value } : null);
              }}
              placeholder="Enter 6-digit pincode"
              maxLength="6"
              className={selectedLocation?.pincode && !validatePincode(selectedLocation.pincode) ? 'error' : ''}
            />
            {selectedLocation?.pincode && !validatePincode(selectedLocation.pincode) && (
              <span className="error-message">Please enter a valid 6-digit pincode</span>
            )}
          </div>
        </div>

        {/* Error Message */}
        {error && (
          <div className="error-message">
            <FontAwesomeIcon icon={faTimes} />
            <span>{error}</span>
          </div>
        )}

        {/* Action Buttons */}
        <div className="action-buttons">
          <button
            className="cancel-button"
            onClick={onBack}
          >
            Cancel
          </button>
          <button
            className="confirm-button"
            onClick={handleSubmit}
            disabled={!selectedLocation}
          >
            <FontAwesomeIcon icon={faCheck} />
            Confirm Address
          </button>
        </div>
      </div>
    </div>
  );
};

export default AddressSelection; 