import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEnvelope, faSpinner, faArrowRight, faKey } from '@fortawesome/free-solid-svg-icons';
import { supabase } from '../lib/supabase';
import './LoginPage.css';

const LoginPage = ({ onNavigate }) => {
  const [email, setEmail] = useState('');
  const [otp, setOtp] = useState('');
  const [isOtpSent, setIsOtpSent] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [messageType, setMessageType] = useState(''); // 'success' or 'error'

  const validateEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  const handleSendOtp = async (e) => {
    e.preventDefault();
    
    if (!email.trim()) {
      setMessage('Please enter your email address');
      setMessageType('error');
      return;
    }

    if (!validateEmail(email)) {
      setMessage('Please enter a valid email address');
      setMessageType('error');
      return;
    }

    setIsLoading(true);
    setMessage('');

    try {
      // Send OTP
      const { error } = await supabase.auth.signInWithOtp({
        email: email.trim(),
        options: {
          shouldCreateUser: false // Don't create user if doesn't exist
        }
      });

      if (error) {
        if (error.message.includes('Signups not allowed')) {
          // User doesn't exist, redirect to signup with prefilled email
          setMessage('Account not found. Redirecting to signup...');
          setMessageType('error');
          setTimeout(() => {
            onNavigate('signup', { email: email.trim() });
          }, 2000);
        } else {
          throw error;
        }
      } else {
        // OTP sent successfully
        setIsOtpSent(true);
        setMessage('OTP sent to your email! Please check and enter the code.');
        setMessageType('success');
      }
    } catch (error) {
      console.error('OTP send error:', error);
      setMessage('An error occurred. Please try again.');
      setMessageType('error');
    } finally {
      setIsLoading(false);
    }
  };

  const handleVerifyOtp = async (e) => {
    e.preventDefault();
    
    if (!otp.trim()) {
      setMessage('Please enter the OTP');
      setMessageType('error');
      return;
    }

    if (otp.trim().length !== 6) {
      setMessage('Please enter a valid 6-digit OTP');
      setMessageType('error');
      return;
    }

    setIsLoading(true);
    setMessage('');

    try {
      // Verify OTP
      const { error } = await supabase.auth.verifyOtp({
        email: email.trim(),
        token: otp.trim(),
        type: 'email'
      });

      if (error) {
        throw error;
      } else {
        // OTP verified successfully, user is now logged in
        setMessage('Login successful! Redirecting...');
        setMessageType('success');
        // The AppRouter will handle the redirect based on auth state
      }
    } catch (error) {
      console.error('OTP verification error:', error);
      setMessage('Invalid OTP. Please try again.');
      setMessageType('error');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="login-page">
      <div className="login-container">
        {/* Header */}
        <div className="login-header">
          <img 
            src={`${process.env.PUBLIC_URL}/images/myna-logo.jpg`} 
            alt="Myna Kitchen" 
            className="logo"
          />
          <h1>Welcome Back</h1>
          <p>Sign in to your Myna Kitchen account</p>
        </div>

        {/* Login Form */}
        {!isOtpSent ? (
          <form onSubmit={handleSendOtp} className="login-form">
            <div className="form-group">
              <label htmlFor="email">
                <FontAwesomeIcon icon={faEnvelope} />
                Email Address
              </label>
              <input
                type="email"
                id="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder="Enter your email address"
                disabled={isLoading}
                className={messageType === 'error' ? 'error' : ''}
              />
            </div>

            {/* Message Display */}
            {message && (
              <div className={`message ${messageType}`}>
                <span>{message}</span>
              </div>
            )}

            {/* Submit Button */}
            <button
              type="submit"
              className="submit-button"
              disabled={isLoading}
            >
              {isLoading ? (
                <>
                  <FontAwesomeIcon icon={faSpinner} spin />
                  Sending OTP...
                </>
              ) : (
                <>
                  Send OTP
                  <FontAwesomeIcon icon={faArrowRight} />
                </>
              )}
            </button>
          </form>
        ) : (
          <form onSubmit={handleVerifyOtp} className="login-form">
            <div className="form-group">
              <label htmlFor="otp">
                <FontAwesomeIcon icon={faKey} />
                Enter OTP
              </label>
              <input
                type="text"
                id="otp"
                value={otp}
                onChange={(e) => {
                  const value = e.target.value.replace(/\D/g, '').slice(0, 6);
                  setOtp(value);
                }}
                placeholder="Enter 6-digit OTP"
                maxLength="6"
                disabled={isLoading}
                className={messageType === 'error' ? 'error' : ''}
                autoFocus
              />
              <p className="otp-info">
                We've sent a 6-digit code to <strong>{email}</strong>
              </p>
            </div>

            {/* Message Display */}
            {message && (
              <div className={`message ${messageType}`}>
                <span>{message}</span>
              </div>
            )}

            {/* Action Buttons */}
            <div className="otp-actions">
              <button
                type="submit"
                className="submit-button"
                disabled={isLoading}
              >
                {isLoading ? (
                  <>
                    <FontAwesomeIcon icon={faSpinner} spin />
                    Verifying...
                  </>
                ) : (
                  <>
                    Verify OTP
                    <FontAwesomeIcon icon={faArrowRight} />
                  </>
                )}
              </button>
              
              <button
                type="button"
                className="resend-button"
                onClick={() => {
                  setIsOtpSent(false);
                  setOtp('');
                  setMessage('');
                }}
                disabled={isLoading}
              >
                Change Email
              </button>
            </div>
          </form>
        )}
      </div>
    </div>
  );
};

export default LoginPage; 