import React, { useState, useEffect } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome, faBuilding, faCheck, faSpinner, faArrowRight } from '@fortawesome/free-solid-svg-icons';
import { supabase } from '../lib/supabase';
import AddressSelection from './AddressSelection';
import './AddressSetupPage.css';

const AddressSetupPage = ({ onNavigate }) => {
  const [addresses, setAddresses] = useState({
    home: null,
    office: null
  });
  const [isLoading, setIsLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [messageType, setMessageType] = useState('');
  const [showAddressSelection, setShowAddressSelection] = useState(false);
  const [addressType, setAddressType] = useState(null);
  const [user, setUser] = useState(null);

  // Load existing addresses if any
  useEffect(() => {
    loadExistingAddresses();
  }, []);

  const loadExistingAddresses = async () => {
    try {
      const { data: { user } } = await supabase.auth.getUser();
      if (user) {
        setUser(user);
        
        const { data: existingAddresses } = await supabase
          .schema('myna')
          .from('addresses')
          .select('*')
          .eq('user_id', user.id);
        
        if (existingAddresses) {
          const homeAddress = existingAddresses.find(addr => addr.label === 'home');
          const officeAddress = existingAddresses.find(addr => addr.label === 'office');
          
          setAddresses({
            home: homeAddress || null,
            office: officeAddress || null
          });
        }
      }
    } catch (error) {
      console.error('Error loading addresses:', error);
    }
  };

  const handleAddressSelection = (addressData) => {
    setAddresses(prev => ({
      ...prev,
      [addressType]: addressData
    }));
    setShowAddressSelection(false);
    setAddressType(null);
  };

  const openAddressSelection = (type) => {
    setAddressType(type);
    setShowAddressSelection(true);
  };

  const validateAddresses = () => {
    if (!addresses.home) {
      setMessage('Please add your home address');
      setMessageType('error');
      return false;
    }

    // Check if home address has pincode
    if (!addresses.home.pincode) {
      setMessage('Please ensure your home address has a valid pincode');
      setMessageType('error');
      return false;
    }

    return true;
  };

  const saveAddressesToSupabase = async () => {
    if (!user) {
      setMessage('User not authenticated');
      setMessageType('error');
      return;
    }

    setIsLoading(true);
    setMessage('');

    try {
      // First, ensure the user profile exists in myna.profiles
      console.log('Checking if user profile exists for user:', user.id);
      
      const { data: existingProfile, error: profileCheckError } = await supabase
        .schema('myna')
        .from('profiles')
        .select('id')
        .eq('id', user.id)
        .single();

      if (profileCheckError && profileCheckError.code !== 'PGRST116') {
        // PGRST116 is "not found" error, which is expected if profile doesn't exist
        console.error('Error checking profile:', profileCheckError);
        throw profileCheckError;
      }

      if (!existingProfile) {
        console.log('Profile does not exist, creating profile for user:', user.id);
        
        // Get stored user data from sessionStorage (from signup)
        const storedUserData = sessionStorage.getItem('userData');
        const userData = storedUserData ? JSON.parse(storedUserData) : {};
        
        const phoneNumber = userData.phone || user.user_metadata?.phone || '';
        const phoneWithPrefix = phoneNumber && !phoneNumber.startsWith('+91') ? `+91${phoneNumber}` : phoneNumber;
        const { error: profileCreateError } = await supabase
          .schema('myna')
          .from('profiles')
          .insert({
            id: user.id,
            name: userData.fullName || user.user_metadata?.full_name || 'User',
            email: userData.email || user.email || '',
            phone_number: phoneWithPrefix
          });

        if (profileCreateError) {
          console.error('Error creating profile:', profileCreateError);
          throw profileCreateError;
        }
        
        console.log('Profile created successfully');
      } else {
        console.log('Profile already exists');
      }

      const addressesToSave = [];

      // Prepare home address
      if (addresses.home) {
        const homeAddress = {
          user_id: user.id,
          label: 'home',
          line1: addresses.home.houseNumber || '',
          line2: addresses.home.landmark || '',
          pincode: addresses.home.pincode,
          latitude: addresses.home.coordinates?.lat || null,
          longitude: addresses.home.coordinates?.lng || null,
          is_default: true, // Home address is default
          formatted_address: addresses.home.formattedAddress || '',
          address_components: addresses.home.addressComponents || []
        };

        // Only add id if it exists (for updates)
        if (addresses.home.id) {
          homeAddress.id = addresses.home.id;
        }

        addressesToSave.push(homeAddress);
      }

      // Prepare office address
      if (addresses.office) {
        const officeAddress = {
          user_id: user.id,
          label: 'office',
          line1: addresses.office.houseNumber || '',
          line2: addresses.office.landmark || '',
          pincode: addresses.office.pincode,
          latitude: addresses.office.coordinates?.lat || null,
          longitude: addresses.office.coordinates?.lng || null,
          is_default: false, // Office address is not default
          formatted_address: addresses.office.formattedAddress || '',
          address_components: addresses.office.addressComponents || []
        };

        // Only add id if it exists (for updates)
        if (addresses.office.id) {
          officeAddress.id = addresses.office.id;
        }

        addressesToSave.push(officeAddress);
      }

      // Save addresses to Supabase - handle each address separately
      for (const address of addressesToSave) {
        if (address.id) {
          // Update existing address
          const { error: updateError } = await supabase
            .schema('myna')
            .from('addresses')
            .update(address)
            .eq('id', address.id);
          
          if (updateError) throw updateError;
        } else {
          // Insert new address
          const { error: insertError } = await supabase
            .schema('myna')
            .from('addresses')
            .insert(address);
          
          if (insertError) throw insertError;
        }
      }

      setMessage('Addresses saved successfully! Redirecting to dashboard...');
      setMessageType('success');
      
      // Force a page reload to update the AppRouter state
      setTimeout(() => {
        window.location.hash = 'dashboard';
        window.location.reload();
      }, 2000);

    } catch (error) {
      console.error('Error saving addresses:', error);
      setMessage('An error occurred while saving addresses. Please try again.');
      setMessageType('error');
    } finally {
      setIsLoading(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateAddresses()) {
      return;
    }

    await saveAddressesToSupabase();
  };

  const getAddressDisplay = (address) => {
    if (!address) return 'Not set';
    
    const parts = [];
    if (address.line1 || address.house_number || address.houseNumber) {
      parts.push(address.line1 || address.house_number || address.houseNumber);
    }
    if (address.formatted_address || address.formattedAddress) {
      parts.push(address.formatted_address || address.formattedAddress);
    }
    if (address.line2 || address.landmark) {
      parts.push(`Near ${address.line2 || address.landmark}`);
    }
    if (address.pincode) {
      parts.push(`Pincode: ${address.pincode}`);
    }
    
    return parts.join(', ') || 'Address set';
  };

  if (showAddressSelection) {
    return (
      <AddressSelection
        addressType={addressType}
        existingAddress={addresses[addressType]}
        onAddressSelect={handleAddressSelection}
        onBack={() => {
          setShowAddressSelection(false);
          setAddressType(null);
        }}
      />
    );
  }

  return (
    <div className="address-setup-page">
      <div className="address-setup-container">
        {/* Header */}
        <div className="address-setup-header">
          <img 
            src={`${process.env.PUBLIC_URL}/images/myna-logo.jpg`} 
            alt="Myna Kitchen" 
            className="logo"
          />
          <h1>Complete Your Profile</h1>
          <p>Add your delivery addresses to get started</p>
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} className="address-setup-form">
          {/* Message Display */}
          {message && (
            <div className={`message ${messageType}`}>
              <span>{message}</span>
            </div>
          )}

          {/* Home Address Section */}
          <div className="address-section">
            <div className="address-header">
              <FontAwesomeIcon icon={faHome} />
              <h2>Home Address</h2>
              <span className="required-badge">Required</span>
            </div>
            
            <div className="address-display">
              <p className="address-text">
                {getAddressDisplay(addresses.home)}
              </p>
              <button
                type="button"
                className="address-button"
                onClick={() => openAddressSelection('home')}
              >
                {addresses.home ? 'Edit Home Address' : 'Add Home Address'}
              </button>
            </div>
          </div>

          {/* Office Address Section */}
          <div className="address-section">
            <div className="address-header">
              <FontAwesomeIcon icon={faBuilding} />
              <h2>Office Address</h2>
              <span className="optional-badge">Optional</span>
            </div>
            
            <div className="address-display">
              <p className="address-text">
                {getAddressDisplay(addresses.office)}
              </p>
              <button
                type="button"
                className="address-button"
                onClick={() => openAddressSelection('office')}
              >
                {addresses.office ? 'Edit Office Address' : 'Add Office Address'}
              </button>
            </div>
          </div>

          {/* Progress Indicator */}
          <div className="progress-section">
            <div className="progress-bar">
              <div 
                className="progress-fill"
                style={{ 
                  width: `${addresses.home ? 50 : 0}%` 
                }}
              ></div>
            </div>
            <p className="progress-text">
              {addresses.home ? 'Home address added' : 'Add home address to continue'}
            </p>
          </div>

          {/* Submit Button */}
          <div className="form-actions">
            <button
              type="submit"
              className="submit-button"
              disabled={isLoading || !addresses.home}
            >
              {isLoading ? (
                <>
                  <FontAwesomeIcon icon={faSpinner} spin />
                  Saving Addresses...
                </>
              ) : (
                <>
                  <FontAwesomeIcon icon={faCheck} />
                  Complete Setup
                  <FontAwesomeIcon icon={faArrowRight} />
                </>
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default AddressSetupPage; 