import React, { useState, useEffect } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUser, faPhone, faEnvelope, faArrowLeft, faCheck, faSpinner } from '@fortawesome/free-solid-svg-icons';
import { supabase } from '../lib/supabase';
import './SignupPage.css';

const SignupPage = ({ onNavigate, prefilledEmail = '' }) => {
  const [formData, setFormData] = useState({
    fullName: '',
    phone: '',
    email: prefilledEmail
  });

  const [errors, setErrors] = useState({});
  const [isLoading, setIsLoading] = useState(false);
  const [isEditMode, setIsEditMode] = useState(false);
  const [message, setMessage] = useState('');
  const [messageType, setMessageType] = useState(''); // 'success' or 'error'

  // Load existing data if in edit mode or handle prefilled email
  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const mode = urlParams.get('mode');
    
    if (mode === 'edit') {
      setIsEditMode(true);
      // Load existing user data from Supabase
      loadUserProfile();
    } else if (prefilledEmail) {
      // Email is prefilled from login page
      setFormData(prev => ({ ...prev, email: prefilledEmail }));
    }
  }, [prefilledEmail]);

  // Load user profile from Supabase
  const loadUserProfile = async () => {
    try {
      const { data: { user } } = await supabase.auth.getUser();
      if (user) {
        const { data: profile } = await supabase
          .schema('myna')
          .from('profiles')
          .select('*')
          .eq('id', user.id)
          .single();
        
        if (profile) {
          setFormData(prev => ({
            ...prev,
            fullName: profile.name || '',
            phone: profile.phone_number || user.user_metadata?.phone || '',
            email: profile.email || user.email || ''
          }));
        }
      }
    } catch (error) {
      console.error('Error loading user profile:', error);
    }
  };

  // Validation functions
  const validatePhone = (phone) => {
    const phoneRegex = /^[6-9]\d{9}$/;
    return phoneRegex.test(phone);
  };

  const validateEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  const validateForm = () => {
    const newErrors = {};

    // Full Name validation
    if (!formData.fullName.trim()) {
      newErrors.fullName = 'Full name is required';
    } else if (formData.fullName.trim().length < 2) {
      newErrors.fullName = 'Full name must be at least 2 characters';
    }

    // Phone validation
    if (!formData.phone) {
      newErrors.phone = 'Phone number is required';
    } else if (!validatePhone(formData.phone)) {
      newErrors.phone = 'Please enter a valid 10-digit phone number';
    }

    // Email validation
    if (!formData.email) {
      newErrors.email = 'Email is required';
    } else if (!validateEmail(formData.email)) {
      newErrors.email = 'Please enter a valid email address';
    }



    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleInputChange = (field, value) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    
    // Clear error when user starts typing
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  const handlePhoneChange = (value) => {
    // Only allow numbers and limit to 10 digits
    const phoneNumber = value.replace(/\D/g, '').slice(0, 10);
    handleInputChange('phone', phoneNumber);
  };



  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setIsLoading(true);
    setMessage('');

    try {
      if (isEditMode) {
        // Update existing profile
        const { data: { user } } = await supabase.auth.getUser();
        if (user) {
          // Update profile in myna.profiles table
          const phoneWithPrefix = formData.phone.startsWith('+91') ? formData.phone : `+91${formData.phone}`;
          const { error: profileError } = await supabase
            .schema('myna')
            .from('profiles')
            .upsert({
              id: user.id,
              name: formData.fullName,
              email: formData.email,
              phone_number: phoneWithPrefix
            });
          
          if (profileError) throw profileError;

          // Update phone in auth.users metadata
          if (phoneWithPrefix !== (user.user_metadata?.phone || '')) {
            const { error: authError } = await supabase.auth.updateUser({
              data: { phone: phoneWithPrefix }
            });

            if (authError) {
              console.error('Error updating phone in auth metadata:', authError);
              // Don't throw error, just log it
            }
          }
          
          setMessage('Profile updated successfully!');
          setMessageType('success');
          setTimeout(() => {
            onNavigate('dashboard');
          }, 2000);
        }
      } else {
        // Create new user account and send OTP
        const phoneWithPrefix = formData.phone.startsWith('+91') ? formData.phone : `+91${formData.phone}`;
        const { data, error } = await supabase.auth.signUp({
          email: formData.email,
          password: generateTemporaryPassword(),
          options: {
            data: {
              full_name: formData.fullName,
              phone: phoneWithPrefix
            }
          }
        });

        if (error) {
          if (error.message.includes('User already registered')) {
            setMessage('An account with this email already exists. Please sign in instead.');
            setMessageType('error');
            setTimeout(() => {
              onNavigate('login');
            }, 3000);
          } else {
            throw error;
          }
        } else {
          console.log('Signup successful, user data:', data);
          
          // Store user data for OTP validation (phone will be updated after OTP verification)
          const userData = {
            email: formData.email,
            fullName: formData.fullName,
            phone: phoneWithPrefix
          };
          sessionStorage.setItem('userData', JSON.stringify(userData));
          console.log('Stored user data for OTP:', userData);
          
          // Redirect to OTP validation page
          console.log('Redirecting to OTP validation page...');
          onNavigate('otp-validation');
        }
      }
    } catch (error) {
      console.error('Error saving data:', error);
      setMessage('An error occurred. Please try again.');
      setMessageType('error');
    } finally {
      setIsLoading(false);
    }
  };

  // Generate temporary password for signup (not used for login)
  const generateTemporaryPassword = () => {
    return Math.random().toString(36).slice(-10) + Math.random().toString(36).slice(-10);
  };





  return (
    <div className="signup-page">
      <div className="signup-container">
        {/* Header */}
        <div className="signup-header">
          <button 
            className="back-button"
            onClick={() => window.history.back()}
          >
            <FontAwesomeIcon icon={faArrowLeft} />
          </button>
          <h1>{isEditMode ? 'Edit Profile' : 'Create Account'}</h1>
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} className="signup-form">
          {/* Message Display */}
          {message && (
            <div className={`message ${messageType}`}>
              <span>{message}</span>
            </div>
          )}

          {/* Basic Details Section */}
          <div className="form-section">            
            {/* Full Name */}
            <div className="form-group">
              <label htmlFor="fullName">
                <FontAwesomeIcon icon={faUser} />
                Full Name *
              </label>
              <input
                type="text"
                id="fullName"
                value={formData.fullName}
                onChange={(e) => handleInputChange('fullName', e.target.value)}
                placeholder="Enter your full name"
                className={errors.fullName ? 'error' : ''}
              />
              {errors.fullName && <span className="error-message">{errors.fullName}</span>}
            </div>

            {/* Phone Number */}
            <div className="form-group">
              <label htmlFor="phone">
                <FontAwesomeIcon icon={faPhone} />
                Phone Number *
              </label>
              <input
                type="tel"
                id="phone"
                value={formData.phone}
                onChange={(e) => handlePhoneChange(e.target.value)}
                placeholder="Enter 10-digit phone number"
                className={errors.phone ? 'error' : ''}
                maxLength="10"
              />
              {errors.phone && <span className="error-message">{errors.phone}</span>}
            </div>

            {/* Email */}
            <div className="form-group">
              <label htmlFor="email">
                <FontAwesomeIcon icon={faEnvelope} />
                Email Address *
              </label>
              <input
                type="email"
                id="email"
                value={formData.email}
                onChange={(e) => handleInputChange('email', e.target.value)}
                placeholder="Enter your email address"
                className={errors.email ? 'error' : ''}
                readOnly={!!prefilledEmail}
              />
              {prefilledEmail && (
                <p className="email-info">
                  Email address from login page
                </p>
              )}
              {errors.email && <span className="error-message">{errors.email}</span>}
            </div>
          </div>



          {/* Submit Button */}
          <div className="form-actions">
            <button
              type="submit"
              className="submit-button"
              disabled={isLoading}
            >
              {isLoading ? (
                <>
                  <FontAwesomeIcon icon={faSpinner} spin />
                  Processing...
                </>
              ) : (
                <>
                  <FontAwesomeIcon icon={faCheck} />
                  {isEditMode ? 'Update Profile' : 'Create Account'}
                </>
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default SignupPage; 