import React, { useState, useEffect } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { 
  faUser, 
  faHome, 
  faBuilding, 
  faSignOutAlt, 
  faEdit,
  faPhone,
  faEnvelope,
  faSpinner,
  faClipboardList,
  faUserCircle
} from '@fortawesome/free-solid-svg-icons';
import { supabase } from '../lib/supabase';
import AddressSelection from './AddressSelection';
import './DashboardPage.css';

const DashboardPage = ({ onNavigate }) => {
  const [activeTab, setActiveTab] = useState('orders');
  const [user, setUser] = useState(null);
  const [profile, setProfile] = useState(null);
  const [addresses, setAddresses] = useState({ home: null, office: null });
  const [isLoading, setIsLoading] = useState(true);
  const [showAddressSelection, setShowAddressSelection] = useState(false);
  const [addressType, setAddressType] = useState(null);
  const [isEditingProfile, setIsEditingProfile] = useState(false);
  const [editForm, setEditForm] = useState({});
  const [creatorUserId, setCreatorUserId] = useState(null);
  const [ordersTabLoaded, setOrdersTabLoaded] = useState(false);
  const [isLoadingCreator, setIsLoadingCreator] = useState(false);
  const [dataLoaded, setDataLoaded] = useState(false);

  useEffect(() => {
    console.log('=== DASHBOARD MOUNT - Loading initial data ===');
    loadUserData();
    
    // Check current hash for tab
    const hash = window.location.hash.slice(1);
    let initialTab = 'orders'; // Default
    
    // Map routes to tabs
    if (hash === 'app/profile') {
      initialTab = 'profile';
      console.log('Setting tab from current hash: profile');
    } else if (hash === 'app/orders') {
      initialTab = 'orders';
      console.log('Setting tab from current hash: orders');
    } else if (hash === 'app/dashboard') {
      // Default to orders for generic dashboard
      initialTab = 'orders';
      console.log('Dashboard route detected, defaulting to orders');
    }
    
    setActiveTab(initialTab);
    
    // Ensure URL reflects the active tab (redirect dashboard to specific tab)
    if (hash === 'app/dashboard' || window.location.hash !== `#app/${initialTab}`) {
      window.location.hash = `app/${initialTab}`;
    }
  }, []);

  // Listen for hash changes to update active tab
  useEffect(() => {
    const handleHashChange = () => {
      const hash = window.location.hash.slice(1); // Remove the #
      
      // Map routes to tabs
      if (hash === 'app/profile') {
        console.log('Hash changed, updating tab to: profile');
        setActiveTab('profile');
      } else if (hash === 'app/orders') {
        console.log('Hash changed, updating tab to: orders');
        setActiveTab('orders');
      } else if (hash === 'app/dashboard') {
        console.log('Hash changed to dashboard, defaulting to orders');
        setActiveTab('orders');
        window.location.hash = 'app/orders';
      }
    };

    window.addEventListener('hashchange', handleHashChange);
    return () => window.removeEventListener('hashchange', handleHashChange);
  }, []);

  // Load creator data only when orders tab is accessed
  useEffect(() => {
    if (activeTab === 'orders' && !ordersTabLoaded) {
      console.log('=== ORDERS TAB ACCESSED - Loading creator data (optimized) ===');
      getCreatorUserId();
      setOrdersTabLoaded(true);
    }
  }, [activeTab, ordersTabLoaded]);

  const loadUserData = async (forceReload = false) => {
    // Prevent duplicate calls if data is already loaded (except for explicit reloads)
    if (dataLoaded && !forceReload) {
      console.log('User data already loaded, skipping duplicate call');
      return;
    }
    
    try {
      console.log('=== LOADING USER DATA (optimized) ===');
      setIsLoading(true);
      const { data: { user } } = await supabase.auth.getUser();
      if (user) {
        setUser(user);
        
        // Load profile from myna.profiles
        const { data: profileData } = await supabase
          .schema('myna')
          .from('profiles')
          .select('*')
          .eq('id', user.id)
          .single();
        
        setProfile(profileData || {});
        setEditForm({
          full_name: profileData?.name || '',
          phone: profileData?.phone_number || user.user_metadata?.phone || '',
          email: profileData?.email || user.email || ''
        });
        
        // Load addresses from myna.addresses
        const { data: addressesData } = await supabase
          .schema('myna')
          .from('addresses')
          .select('*')
          .eq('user_id', user.id);
        
        if (addressesData) {
          const homeAddress = addressesData.find(addr => addr.label === 'home');
          const officeAddress = addressesData.find(addr => addr.label === 'office');
          
          setAddresses({
            home: homeAddress || null,
            office: officeAddress || null
          });
        }

        // Creator userid will be loaded when orders tab is accessed
      }
    } catch (error) {
      console.error('Error loading user data:', error);
    } finally {
      setIsLoading(false);
      setDataLoaded(true);
    }
  };

  const creatorSync = async (accessToken) => {
    try {
      console.log('Calling creatorSync...');
      const response = await fetch('https://yqymdsmhxsijlmrvlczo.supabase.co/functions/v1/creatorSync', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${accessToken}`,
          'Content-Type': 'application/json'
        }
      });

      if (response.ok) {
        const data = await response.json();
        console.log('Creator sync response:', data);
        return data;
      } else {
        console.error('Failed to sync creator:', response.status, response.statusText);
        return null;
      }
    } catch (error) {
      console.error('Error syncing creator:', error);
      return null;
    }
  };

  const getCreatorUserId = async () => {
    if (isLoadingCreator) {
      console.log('Creator userid already loading, skipping duplicate call');
      return;
    }
    
    try {
      setIsLoadingCreator(true);
      console.log('Getting creator userid...');
      const { data: { session } } = await supabase.auth.getSession();
      if (!session?.access_token) {
        console.error('No access token available');
        return;
      }

      const response = await fetch('https://yqymdsmhxsijlmrvlczo.supabase.co/functions/v1/getCreatorUserid', {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${session.access_token}`,
          'Content-Type': 'application/json'
        }
      });

      if (response.ok) {
        const data = await response.json();
        console.log('Creator userid response:', data);
        
        // Check if sync is needed
        if (!data.is_synced) {
          console.log('User not synced, calling creatorSync...');
          await creatorSync(session.access_token);
          // After sync, get the creator userid again
          const syncResponse = await fetch('https://yqymdsmhxsijlmrvlczo.supabase.co/functions/v1/getCreatorUserid', {
            method: 'GET',
            headers: {
              'Authorization': `Bearer ${session.access_token}`,
              'Content-Type': 'application/json'
            }
          });
          
          if (syncResponse.ok) {
            const syncData = await syncResponse.json();
            console.log('Creator userid after sync:', syncData);
            setCreatorUserId(syncData.creator_userid);
          }
        } else {
          console.log('User already synced, using existing creator userid');
          setCreatorUserId(data.creator_userid);
        }
      } else {
        console.error('Failed to get creator userid:', response.status, response.statusText);
      }
    } catch (error) {
      console.error('Error getting creator userid:', error);
    } finally {
      setIsLoadingCreator(false);
    }
  };

  const handleLogout = async () => {
    try {
      await supabase.auth.signOut();
      onNavigate('login');
    } catch (error) {
      console.error('Error signing out:', error);
    }
  };

  // This function is now handled by handleAddressSave
  // const handleAddressSelection = (addressData) => {
  //   setAddresses(prev => ({
  //     ...prev,
  //     [addressType]: addressData
  //   }));
  //   setShowAddressSelection(false);
  //   setAddressType(null);
  // };

  const openAddressSelection = (type) => {
    setAddressType(type);
    setShowAddressSelection(true);
    // Note: Creator data doesn't need to be refreshed just for address editing
    // Only reset if there's a specific business need to refresh creator data
  };

    const saveAddressToSupabase = async (addressData, type) => {
    try {
      console.log('=== ADDRESS SAVE DEBUG ===');
      console.log('addressData received:', addressData);
      console.log('address type:', type);
      console.log('current addresses state:', addresses);
      console.log('existing address for type:', addresses[type]);

      const addressToSave = {
        user_id: user.id,
        label: type,
        line1: addressData.houseNumber || addressData.line1 || '',
        line2: addressData.landmark || addressData.line2 || '',
        pincode: addressData.pincode,
        latitude: addressData.coordinates?.lat || addressData.lat || addressData.latitude || null,
        longitude: addressData.coordinates?.lng || addressData.lng || addressData.longitude || null,
        is_default: type === 'home',
        formatted_address: addressData.formattedAddress || addressData.formatted_address || '',
        address_components: addressData.addressComponents || addressData.address_components || []
      };

      console.log('addressToSave object:', addressToSave);

      // Check if there's an existing address for this user and type
      const existingAddressId = addressData.id || addresses[type]?.id;
      console.log('existingAddressId:', existingAddressId);

      if (existingAddressId) {
        // Update existing address
        console.log('Updating existing address with ID:', existingAddressId);
        const { data, error, status } = await supabase
          .schema('myna')
          .from('addresses')
          .update(addressToSave)
          .eq('id', existingAddressId)
          .select(); // Add select to get response data
        
        console.log('Update response:', { data, error, status });
        
        if (error) throw error;
        console.log('Address updated successfully');
      } else {
        // Check if there's already an address with this label for this user
        console.log('No existing ID found, checking for existing address by user+type');
        const { data: existingAddresses, error: checkError } = await supabase
          .schema('myna')
          .from('addresses')
          .select('*')
          .eq('user_id', user.id)
          .eq('label', type);

        console.log('Existing addresses query result:', existingAddresses);
        if (checkError) throw checkError;

        if (existingAddresses && existingAddresses.length > 0) {
          // Update the existing address
          console.log('Found existing address for type:', type, 'updating it');
          const { data, error, status } = await supabase
            .schema('myna')
            .from('addresses')
            .update(addressToSave)
            .eq('id', existingAddresses[0].id)
            .select(); // Add select to get response data
          
          console.log('Update response:', { data, error, status });
          if (error) throw error;
          console.log('Address updated successfully');
        } else {
          // Insert new address
          console.log('Creating new address for type:', type);
          const { data, error, status } = await supabase
            .schema('myna')
            .from('addresses')
            .insert(addressToSave)
            .select(); // Add select to get response data
          
          console.log('Insert response:', { data, error, status });
          if (error) throw error;
          console.log('Address created successfully');
        }
      }
    } catch (error) {
      console.error('Error saving address:', error);
      alert('Error saving address. Please try again.');
    }
  };

  // Optimized function to reload only addresses (not user/profile data)
  const reloadAddresses = async () => {
    try {
      if (!user?.id) return;
      
      console.log('Reloading addresses only...');
      const { data: addressesData } = await supabase
        .schema('myna')
        .from('addresses')
        .select('*')
        .eq('user_id', user.id);
      
      if (addressesData) {
        const homeAddress = addressesData.find(addr => addr.label === 'home');
        const officeAddress = addressesData.find(addr => addr.label === 'office');
        
        setAddresses({
          home: homeAddress || null,
          office: officeAddress || null
        });
      }
      console.log('Addresses reloaded successfully');
    } catch (error) {
      console.error('Error reloading addresses:', error);
    }
  };

  const handleAddressSave = async (addressData) => {
    try {
      console.log('=== HANDLE ADDRESS SAVE ===');
      console.log('addressData:', addressData);
      console.log('addressType:', addressType);
      
      await saveAddressToSupabase(addressData, addressType);
      
      console.log('Address save completed, reloading addresses...');
      // Reload only addresses (not full user data)
      await reloadAddresses();
      console.log('Addresses reloaded');
      
      // Close address selection
      setShowAddressSelection(false);
      setAddressType(null);
    } catch (error) {
      console.error('Error saving address:', error);
      alert('Error saving address. Please try again.');
    }
  };

  const handleProfileUpdate = async () => {
    try {
      // Update profile in myna.profiles table
      const phoneWithPrefix = editForm.phone.startsWith('+91') ? editForm.phone : `+91${editForm.phone}`;
      const { error: profileError } = await supabase
        .schema('myna')
        .from('profiles')
        .upsert({
          id: user.id,
          name: editForm.full_name,
          email: editForm.email,
          phone_number: phoneWithPrefix
        });

      if (profileError) throw profileError;

      // Update phone in auth.users metadata
      if (phoneWithPrefix !== (user.user_metadata?.phone || '')) {
        const { error: authError } = await supabase.auth.updateUser({
          data: { phone: phoneWithPrefix }
        });

        if (authError) {
          console.error('Error updating phone in auth metadata:', authError);
          // Don't throw error, just log it as phone update is less critical
        }
      }

      setProfile(prev => ({ 
        ...prev, 
        name: editForm.full_name,
        email: editForm.email,
        phone_number: phoneWithPrefix
      }));
      setIsEditingProfile(false);
      alert('Profile updated successfully!');
    } catch (error) {
      console.error('Error updating profile:', error);
      alert('Error updating profile. Please try again.');
    }
  };

  const getAddressDisplay = (address) => {
    if (!address) return 'Not set';
    
    const parts = [];
    if (address.line1 || address.house_number) parts.push(address.line1 || address.house_number);
    if (address.formatted_address) parts.push(address.formatted_address);
    if (address.line2 || address.landmark) parts.push(`Near ${address.line2 || address.landmark}`);
    if (address.pincode) parts.push(`Pincode: ${address.pincode}`);
    
    return parts.join(', ') || 'Address set';
  };

  if (isLoading) {
    return (
      <div className="dashboard-loading">
        <FontAwesomeIcon icon={faSpinner} spin />
        <p>Loading your dashboard...</p>
      </div>
    );
  }

  if (showAddressSelection) {
    return (
      <AddressSelection
        addressType={addressType}
        existingAddress={addresses[addressType]}
        onAddressSelect={handleAddressSave}
        onBack={() => {
          setShowAddressSelection(false);
          setAddressType(null);
        }}
      />
    );
  }

  return (
    <div className="dashboard-page">
      {/* Tab Navigation */}
      <div className="tab-navigation">
        <button
          className={`tab-button ${activeTab === 'orders' ? 'active' : ''}`}
          onClick={() => {
            setActiveTab('orders');
            window.location.hash = 'app/orders';
          }}
        >
          <FontAwesomeIcon icon={faClipboardList} />
          Orders
        </button>
        <button
          className={`tab-button ${activeTab === 'profile' ? 'active' : ''}`}
          onClick={() => {
            setActiveTab('profile');
            window.location.hash = 'app/profile';
          }}
        >
          <FontAwesomeIcon icon={faUserCircle} />
          Profile
        </button>
      </div>

      {/* Tab Content */}
      <div className="tab-content">
        {activeTab === 'orders' && (
          <div className="orders-tab">
            {creatorUserId ? (
              <iframe
                src={`https://creatorapp.zohopublic.in/kitchenmyna/myna/page-embed/Website_user_page/kJqYfxaBrYZDGm1X9VxFSGD30NqUbjXbqZt28nvMQYQMu2YFT4mYWkAAgMjV1WHAK1NaT4yeCv0XdWKR5rneZpBbaZXMGfEKe7Uy?userid=${creatorUserId}`}
                title="Orders Management"
                className="orders-iframe-full"
                frameBorder="0"
                allowFullScreen
              />
            ) : (
              <div className="iframe-loading">
                <FontAwesomeIcon icon={faSpinner} spin />
                <p>Loading orders...</p>
              </div>
            )}
          </div>
        )}

        {activeTab === 'profile' && (
          <div className="profile-tab">
            <div className="profile-section">
              <div className="profile-info">
                <div className="info-item">
                  <FontAwesomeIcon icon={faUser} />
                  <span><strong>Name:</strong> {profile?.name || 'Not set'}</span>
                </div>
                <div className="info-item">
                  <FontAwesomeIcon icon={faPhone} />
                  <span><strong>Phone:</strong> {profile?.phone_number || user?.user_metadata?.phone || 'Not set'}</span>
                </div>
                <div className="info-item">
                  <FontAwesomeIcon icon={faEnvelope} />
                  <span><strong>Email:</strong> {profile?.email || user?.email || 'Not set'}</span>
                </div>
              </div>
            </div>

            <div className="profile-section">
              <div className="address-header">
                <h2>
                  <FontAwesomeIcon icon={faHome} />
                  Home Address
                </h2>
                <button
                  className="edit-address-btn"
                  onClick={() => openAddressSelection('home')}
                  title={addresses.home ? 'Edit Home Address' : 'Add Home Address'}
                >
                  <FontAwesomeIcon icon={faEdit} />
                </button>
              </div>
              <div className="address-display">
                <p className="address-text">
                  {getAddressDisplay(addresses.home)}
                </p>
              </div>
            </div>

            <div className="profile-section">
              <div className="address-header">
                <h2>
                  <FontAwesomeIcon icon={faBuilding} />
                  Office Address
                </h2>
                <button
                  className="edit-address-btn"
                  onClick={() => openAddressSelection('office')}
                  title={addresses.office ? 'Edit Office Address' : 'Add Office Address'}
                >
                  <FontAwesomeIcon icon={faEdit} />
                </button>
              </div>
              <div className="address-display">
                <p className="address-text">
                  {getAddressDisplay(addresses.office)}
                </p>
              </div>
            </div>

            <div className="profile-section logout-section">
              <button onClick={handleLogout} className="logout-button-bottom">
                <FontAwesomeIcon icon={faSignOutAlt} />
                Logout
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default DashboardPage; 