import React, { useState, useEffect } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faKey, faSpinner, faArrowRight, faEnvelope } from '@fortawesome/free-solid-svg-icons';
import { supabase } from '../lib/supabase';
import './OtpValidationPage.css';

const OtpValidationPage = ({ onNavigate, userData }) => {
  const [otp, setOtp] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [messageType, setMessageType] = useState('');
  const [countdown, setCountdown] = useState(30);

  useEffect(() => {
    // Start countdown for resend OTP
    const timer = setInterval(() => {
      setCountdown(prev => {
        if (prev <= 1) {
          clearInterval(timer);
          return 0;
        }
        return prev - 1;
      });
    }, 1000);

    return () => clearInterval(timer);
  }, []);

  const validateOtp = (otp) => {
    if (!otp) return false;
    return /^\d{6}$/.test(otp);
  };

  const handleVerifyOtp = async (e) => {
    e.preventDefault();
    
    if (!otp.trim()) {
      setMessage('Please enter the OTP');
      setMessageType('error');
      return;
    }

    if (!validateOtp(otp.trim())) {
      setMessage('Please enter a valid 6-digit OTP');
      setMessageType('error');
      return;
    }

    setIsLoading(true);
    setMessage('');

    try {
      // Verify OTP
      const { error } = await supabase.auth.verifyOtp({
        email: userData.email,
        token: otp.trim(),
        type: 'signup'
      });

      if (error) {
        throw error;
      } else {
        // OTP verified successfully, user is now logged in
        console.log('OTP verified successfully, creating profile...');
        
        // Create profile record after successful authentication
        try {
          const { data: { user } } = await supabase.auth.getUser();
          if (user && userData) {
            console.log('Creating profile for authenticated user:', user.id);
            
            // Check if profile already exists first
            const { data: existingProfile, error: checkError } = await supabase
              .schema('myna')
              .from('profiles')
              .select('id')
              .eq('id', user.id)
              .single();

            if (checkError && checkError.code !== 'PGRST116') {
              // PGRST116 is "not found" error, which is expected if profile doesn't exist
              console.error('Error checking existing profile:', checkError);
            }

            if (!existingProfile) {
              // Profile doesn't exist, create it
              const phoneWithPrefix = userData.phone.startsWith('+91') ? userData.phone : `+91${userData.phone}`;
              const { error: profileError } = await supabase
                .schema('myna')
                .from('profiles')
                .insert({
                  id: user.id,
                  name: userData.fullName,
                  email: userData.email,
                  phone_number: phoneWithPrefix
                });

              if (profileError) {
                console.error('Profile creation error:', profileError);
                // Don't fail the whole process if profile creation fails
                // We'll handle it later in AddressSetupPage
              } else {
                console.log('Profile created successfully');
              }
            } else {
              console.log('Profile already exists, skipping creation');
            }
          }
        } catch (profileError) {
          console.error('Profile creation error:', profileError);
          // Don't fail the whole process if profile creation fails
        }

        setMessage('Email verified successfully! Redirecting to complete your profile...');
        setMessageType('success');
        setTimeout(() => {
          onNavigate('address-setup');
        }, 2000);
      }
    } catch (error) {
      console.error('OTP verification error:', error);
      setMessage('Invalid OTP. Please try again.');
      setMessageType('error');
    } finally {
      setIsLoading(false);
    }
  };

  const handleResendOtp = async () => {
    setIsLoading(true);
    setMessage('');

    try {
      const phoneWithPrefix = userData.phone.startsWith('+91') ? userData.phone : `+91${userData.phone}`;
      const { error } = await supabase.auth.signUp({
        email: userData.email,
        password: Math.random().toString(36).slice(-10), // Temporary password
        options: {
          data: {
            full_name: userData.fullName,
            phone: phoneWithPrefix
          }
        }
      });

      if (error) {
        throw error;
      } else {
        setMessage('OTP resent successfully! Please check your email.');
        setMessageType('success');
        setCountdown(30);
      }
    } catch (error) {
      console.error('Resend OTP error:', error);
      setMessage('Failed to resend OTP. Please try again.');
      setMessageType('error');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="otp-validation-page">
      <div className="otp-validation-container">
        {/* Header */}
        <div className="otp-validation-header">
          <img 
            src={`${process.env.PUBLIC_URL}/images/myna-logo.jpg`} 
            alt="Myna Kitchen" 
            className="logo"
          />
          <h1>Verify Your Email</h1>
          <p>We've sent a 6-digit code to your email</p>
        </div>

        {/* Form */}
        <form onSubmit={handleVerifyOtp} className="otp-validation-form">
          {/* Email Display */}
          <div className="email-display">
            <FontAwesomeIcon icon={faEnvelope} />
            <span>{userData?.email}</span>
          </div>

          {/* OTP Input */}
          <div className="form-group">
            <label htmlFor="otp">
              <FontAwesomeIcon icon={faKey} />
              Enter OTP
            </label>
            <input
              type="text"
              id="otp"
              value={otp}
              onChange={(e) => {
                const value = e.target.value.replace(/\D/g, '').slice(0, 6);
                setOtp(value);
              }}
              placeholder="Enter 6-digit OTP"
              maxLength="6"
              disabled={isLoading}
              className={messageType === 'error' ? 'error' : ''}
              autoFocus
            />
          </div>

          {/* Message Display */}
          {message && (
            <div className={`message ${messageType}`}>
              <span>{message}</span>
            </div>
          )}

          {/* Submit Button */}
          <button
            type="submit"
            className="submit-button"
            disabled={isLoading}
          >
            {isLoading ? (
              <>
                <FontAwesomeIcon icon={faSpinner} spin />
                Verifying...
              </>
            ) : (
              <>
                Verify Email
                <FontAwesomeIcon icon={faArrowRight} />
              </>
            )}
          </button>

          {/* Resend OTP */}
          <div className="resend-section">
            <p>Didn't receive the code?</p>
            <button
              type="button"
              className="resend-button"
              onClick={handleResendOtp}
              disabled={isLoading || countdown > 0}
            >
              {countdown > 0 ? `Resend in ${countdown}s` : 'Resend OTP'}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default OtpValidationPage; 