import React, { useState, useEffect } from 'react';
import { supabase } from '../lib/supabase';
import LoginPage from './LoginPage';
import SignupPage from './SignupPage';
import OtpValidationPage from './OtpValidationPage';
import AddressSetupPage from './AddressSetupPage';
import DashboardPage from './DashboardPage';
import LoadingSpinner from './LoadingSpinner';

const AppRouter = () => {
  const [currentRoute, setCurrentRoute] = useState('');
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [hasAddresses, setHasAddresses] = useState(false);

  // Handle hash-based routing
  useEffect(() => {
    const handleHashChange = () => {
      const hash = window.location.hash.slice(1); // Remove the #
      const [fullRoute, queryString] = hash.split('?');
      
      // Always maintain app/ prefix for app routes
      let route = fullRoute;
      
      // If just 'app' without sub-route, default to app/login
      if (fullRoute === 'app') {
        route = 'app/login';
        window.location.hash = 'app/login';
      }
      // If no route at all, default to app/login
      else if (!fullRoute) {
        route = 'app/login';
        window.location.hash = 'app/login';
      }
      // If it doesn't start with app/, add the prefix
      else if (!fullRoute.startsWith('app/') && !fullRoute.includes('browse-plans')) {
        route = `app/${fullRoute}`;
        window.location.hash = route;
      }
      
      setCurrentRoute(route);
      
      // Parse query parameters
      if (queryString) {
        const params = new URLSearchParams(queryString);
        const email = params.get('email');
        if (email) {
          // Store email for signup page
          sessionStorage.setItem('prefilledEmail', email);
        }
      }
    };

    // Set initial route
    handleHashChange();

    // Listen for hash changes
    window.addEventListener('hashchange', handleHashChange);
    return () => window.removeEventListener('hashchange', handleHashChange);
  }, []);

  // Handle authentication state
  useEffect(() => {
    let isMounted = true;
    
    // Add timeout to prevent infinite loading
    const loadingTimeout = setTimeout(() => {
      if (isMounted) {
        console.warn('Authentication check timeout - setting loading to false');
        setLoading(false);
      }
    }, 10000); // 10 second timeout

    // Get initial session with retry logic
    const getInitialSession = async (retryCount = 0) => {
      try {
        setLoading(true); // <-- Ensure loading is true while session is being checked
        console.log('Getting initial session... (attempt:', retryCount + 1, ')');
        
        // Wait a bit for Supabase to initialize on first load
        if (retryCount === 0) {
          await new Promise(resolve => setTimeout(resolve, 100));
        }
        
        const { data: { session }, error } = await supabase.auth.getSession();
        
        if (error) {
          console.error('Session error:', error);
          if (retryCount < 2) {
            console.log('Retrying session fetch...');
            setTimeout(() => getInitialSession(retryCount + 1), 1000);
            return;
          }
        }
        
        console.log('Session retrieved:', session?.user?.id ? 'User found' : 'No user');
        
        if (isMounted) {
          setUser(session?.user || null);
          
          if (session?.user) {
            // Check if user has completed address setup
            setLoading(true); // <-- Loading true while checking addresses
            await checkAddressSetup(session.user.id);
          }
        }
      } catch (error) {
        console.error('Error getting session:', error);
        if (retryCount < 2) {
          console.log('Retrying session fetch due to error...');
          setTimeout(() => getInitialSession(retryCount + 1), 1000);
          return;
        }
      } finally {
        if (isMounted) {
          clearTimeout(loadingTimeout);
          setLoading(false);
        }
      }
    };

    getInitialSession();

    // Listen for auth changes
    const { data: { subscription } } = supabase.auth.onAuthStateChange(
      async (event, session) => {
        console.log('Auth state change:', event, session?.user?.id || 'No user');
        
        if (!isMounted) return;
        
        setLoading(true);
        
        // Add timeout for auth state change processing
        const authTimeout = setTimeout(() => {
          if (isMounted) {
            console.warn('Auth state change processing timeout - setting loading to false');
            setLoading(false);
          }
        }, 5000); // 5 second timeout for auth state changes
        
        try {
          // Handle different auth events
          if (event === 'SIGNED_IN') {
            console.log('User signed in successfully');
          } else if (event === 'SIGNED_OUT') {
            console.log('User signed out');
          } else if (event === 'TOKEN_REFRESHED') {
            console.log('Token refreshed');
          }
          
          setUser(session?.user || null);
          
          if (session?.user) {
            try {
              await checkAddressSetup(session.user.id);
            } catch (error) {
              console.error('Error checking address setup on auth change:', error);
              setHasAddresses(false);
            }
          } else {
            setHasAddresses(false);
          }
        } catch (error) {
          console.error('Error in auth state change handler:', error);
        } finally {
          clearTimeout(authTimeout);
          if (isMounted) {
            setLoading(false);
          }
        }
      }
    );

    return () => {
      isMounted = false;
      clearTimeout(loadingTimeout);
      subscription.unsubscribe();
    };
  }, []);

  // Check if user has completed address setup
  const checkAddressSetup = async (userId) => {
    return new Promise((resolve) => {
      // Add timeout to prevent hanging
      const addressCheckTimeout = setTimeout(() => {
        console.warn('Address check timeout - assuming no addresses');
        setHasAddresses(false);
        resolve();
      }, 5000); // 5 second timeout
      
      (async () => {
        try {
          console.log('Checking addresses for user:', userId);
          
          // Query myna.addresses table with correct schema and fields
          const { data, error } = await supabase
            .schema('myna')
            .from('addresses')
            .select('id, label, line1, pincode, is_default')
            .eq('user_id', userId);
          
          if (error) {
            console.error('Database error querying myna.addresses:', error);
            // Don't throw error - just log it and continue
            setHasAddresses(false);
            clearTimeout(addressCheckTimeout);
            resolve();
            return;
          }
          
          console.log('Address data from myna.addresses:', data);
          
          // Check if user has at least one address (any label is fine)
          // Based on your table structure, we just need to check if they have any address
          const hasAddresses = data && data.length > 0;
          
          // Optionally, you can check for specific labels like 'home' if needed
          // const hasHome = data && data.length > 0 && data.some(addr => addr.label === 'home');
          
          console.log('Has addresses:', hasAddresses);
          setHasAddresses(hasAddresses);
          clearTimeout(addressCheckTimeout);
          resolve();
        } catch (error) {
          console.error('Error checking address setup:', error);
          // Set to false but don't block the app
          setHasAddresses(false);
          clearTimeout(addressCheckTimeout);
          resolve();
        }
      })();
    });
  };

  // Navigation helper
  const navigateTo = (route, params = {}) => {
    // Ensure all routes have app/ prefix (except browse-plans)
    let fullRoute = route;
    if (!route.startsWith('app/') && !route.includes('browse-plans')) {
      fullRoute = `app/${route}`;
    }
    
    if (params.email) {
      // Store user data for OTP validation
      if (params.fullName && params.phone) {
        sessionStorage.setItem('userData', JSON.stringify({
          email: params.email,
          fullName: params.fullName,
          phone: params.phone
        }));
      }
      window.location.hash = `${fullRoute}?email=${encodeURIComponent(params.email)}`;
    } else {
      window.location.hash = fullRoute;
    }
  };

  // Route protection logic
  const getProtectedRoute = () => {
    if (!user) {
      // Not authenticated - allow login, signup, and OTP validation
      if (currentRoute !== 'app/login' && currentRoute !== 'app/signup' && currentRoute !== 'app/otp-validation') {
        navigateTo('login'); // This will become app/login due to navigateTo logic
        return 'app/login';
      }
      return currentRoute;
    }

    // User is authenticated but hasn't completed OTP validation
    if (currentRoute === 'app/otp-validation') {
      return currentRoute;
    }

    // User is authenticated but hasn't completed address setup
    if (!hasAddresses) {
      if (currentRoute !== 'app/address-setup') {
        navigateTo('address-setup'); // This will become app/address-setup
        return 'app/address-setup';
      }
      return currentRoute;
    }

    // User is authenticated and has addresses - redirect auth pages to dashboard
    if (currentRoute === 'app/login' || currentRoute === 'app/signup' || currentRoute === 'app/otp-validation' || currentRoute === 'app/address-setup') {
      navigateTo('dashboard'); // This will become app/dashboard
      return 'app/dashboard';
    }

    // Default to dashboard for app routes
    if (currentRoute && currentRoute.startsWith('app/')) {
      return currentRoute;
    }

    // Fallback to dashboard
    navigateTo('dashboard');
    return 'app/dashboard';
  };

  // Show loading spinner while checking auth state
  if (loading) {
    return <LoadingSpinner />;
  }

  const protectedRoute = getProtectedRoute();

  // Render the appropriate component based on the route
  switch (protectedRoute) {
    case 'app/login':
      return <LoginPage onNavigate={navigateTo} />;
    
    case 'app/signup':
      const prefilledEmail = sessionStorage.getItem('prefilledEmail');
      return <SignupPage onNavigate={navigateTo} prefilledEmail={prefilledEmail} />;
    
    case 'app/otp-validation':
      const userData = sessionStorage.getItem('userData');
      return <OtpValidationPage onNavigate={navigateTo} userData={userData ? JSON.parse(userData) : null} />;
    
    case 'app/address-setup':
      return <AddressSetupPage onNavigate={navigateTo} />;
    
    case 'app/dashboard':
    case 'app/orders':
    case 'app/profile':
      return <DashboardPage onNavigate={navigateTo} />;
    
    default:
      // Redirect to login for unknown routes
      navigateTo('login');
      return <LoginPage onNavigate={navigateTo} />;
  }
};

export default AppRouter; 