export const supabase = createClient(supabaseUrl, supabaseAnonKey, {
  auth: {
    autoRefreshToken: true,
    persistSession: true,
    detectSessionInUrl: true,
    storageKey: 'myna-auth-token',
    storage: window.localStorage
  }
  // Note: Auth tables use default 'public' schema, app tables use 'myna' schema
});

// Database table names
export const TABLES = {
  PROFILES: 'profiles',
  ADDRESSES: 'addresses'
};

// Helper functions for database operations
export const dbHelpers = {
  // Get user profile from myna.profiles
  async getUserProfile(userId) {
    const { data, error } = await supabase
      .schema('myna')
      .from(TABLES.PROFILES)
      .select('*')
      .eq('id', userId)
      .single();
    
    if (error) throw error;
    return data;
  },

  // Create or update user profile in myna.profiles
  async upsertUserProfile(profile) {
    const { data, error } = await supabase
      .schema('myna')
      .from(TABLES.PROFILES)
      .upsert(profile, { onConflict: 'id' })
      .select()
      .single();
    
    if (error) throw error;
    return data;
  },

  // Get user addresses from myna.addresses
  async getUserAddresses(userId) {
    const { data, error } = await supabase
      .schema('myna')
      .from(TABLES.ADDRESSES)
      .select('*')
      .eq('user_id', userId)
      .order('created_at', { ascending: true });
    
    if (error) throw error;
    return data;
  },

  // Save user address to myna.addresses
  async saveUserAddress(address) {
    const { data, error } = await supabase
      .schema('myna')
      .from(TABLES.ADDRESSES)
      .upsert(address, { onConflict: 'id' })
      .select()
      .single();
    
    if (error) throw error;
    return data;
  },

  // Delete user address from myna.addresses
  async deleteUserAddress(addressId) {
    const { error } = await supabase
      .schema('myna')
      .from(TABLES.ADDRESSES)
      .delete()
      .eq('id', addressId);
    
    if (error) throw error;
  },

  // Check if user has completed address setup (using label field)
  async hasCompletedAddressSetup(userId) {
    const { data, error } = await supabase
      .schema('myna')
      .from(TABLES.ADDRESSES)
      .select('id, label, line1, pincode')
      .eq('user_id', userId);
    
    if (error) throw error;
    
    // Check if user has at least one complete address
    const hasCompleteAddress = data && data.length > 0 && 
      data.some(addr => addr.line1 && addr.pincode);
    
    return hasCompleteAddress;
  }
};

export default supabase; 